/**
 * Base video data provider base model.
 *
 * @author Igor Zaporozhets <deadbyelpy@gmail.com>
 */

'use strict';

/* eslint max-lines-per-function: off */
/* eslint max-len: off */

var Promise = require('../../promise'),
    app = require('mag-app'),

    api = require('./../client.js'),

    ChannelModel = require('./channel'),
    l10n,
    l10nAuthor,
    timeDiff;


/**
 * Search model implementation.
 *
 * @constructor
 * @extends ChannelModel
 *
 * @param {Object} [config={}] filter parameters
 * @param {Object} [config.channel] channel object with id and name
 * @param {string} [config.searchQuery] search query
 * @param {number} [config.relatedToVideoId] pass this param add video relation option
 *
 * @fires SearchModel#content:changed
 */
function SearchModel ( config ) {
    this.pages = {};

    this.searchQuery = '';

    this.relatedToVideoId = '';

    this.channelId = '';

    this.order = '';

    this.type = '';

    ChannelModel.call(this);

    config = config || {};

    this.filter(config);
}

// inheritance
SearchModel.prototype = Object.create(ChannelModel.prototype);
SearchModel.prototype.constructor = SearchModel;


SearchModel.prototype.getPage = function ( params ) {
    var self = this,
        url;

    if ( !l10n ) {
        l10n = require('../../util/l10n');
        timeDiff = require('../../util/time.diff');
        l10nAuthor = gettext('Author');
    }

    return new Promise(function ( resolve, reject ) {
        params.channel = params.channel || self.channel;
        params.type = self.type;
        params.searchQuery = params.searchQuery || self.searchQuery;
        params.page = +params.page || 0;
        params.relatedToVideoId = params.relatedToVideoId || self.relatedToVideoId;

        url = 'search?part=id&maxResults=' + (params.count || 6);
        if ( params.page ) {
            if ( !self.pages[params.page] ) {
                reject();

                return;
            }
            url += '&pageToken=' + self.pages[params.page];
        }
        if ( params.channel && params.channel.id ) {
            url += '&channelId=' + params.channel.id;
        }
        if ( self.order ) {
            url += '&order=' + self.order;
        }
        if ( params.relatedToVideoId ) {
            url += '&type=video&relatedToVideoId=' + params.relatedToVideoId;
        } else if ( params.type ) {
            url += '&type=video';
        }
        if ( params.searchQuery ) {
            url += '&q=' + encodeURIComponent(params.searchQuery);
        }
        if ( app.settings.safeSearch ) {
            url += '&safeSearch=strict';
        }
        api.request('GET', url).then(function ( data ) {
            var result = [],
                totalSize = 0,
                channels = {},
                playlists = {},
                videoIds = '',
                channelIds = '',
                playlistIds = '',
                i;

            data = JSON.parse(data);
            if ( data.nextPageToken ) {
                self.pages[params.page + 1] = data.nextPageToken;
            }
            if ( data.prevPageToken ) {
                self.pages[params.page - 1] = data.prevPageToken;
            }

            data = data.items;
            if ( data.length === 0 ) {
                reject('empty');
            } else {
                totalSize = data.length;
                for ( i = 0; i < totalSize; ++i ) {
                    if ( data[i].id.kind === 'youtube#video' ) {
                        videoIds += data[i].id.videoId + ',';
                    } else if ( data[i].id.kind === 'youtube#channel' ) {
                        channelIds += data[i].id.channelId + ',';
                        channels[i] = 1;
                    } else if ( data[i].id.kind === 'youtube#playlist' ) {
                        playlistIds += data[i].id.playlistId + ',';
                        playlists[i] = 1;
                    }
                }

                Promise.all([
                    self.getMovies(videoIds.substr(0, videoIds.length - 1)),
                    self.getChannelsInfo(channelIds.substr(0, channelIds.length - 1)),
                    self.getTotalInfoPlaylists({id: playlistIds.substr(0, playlistIds.length - 1), channel: false})
                ]).then(function ( thenData ) {
                    var currentDate = +new Date(),
                        videoIndex = 0,
                        channelIndex = 0,
                        playlistIndex = 0;

                    for ( i = 0; i < totalSize; ++i ) {
                        if ( channels[i] && thenData[1][channelIndex] ) {
                            result.push({
                                value: 1,
                                id: thenData[1][channelIndex].id,
                                title: thenData[1][channelIndex].snippet.localized.title,
                                icon: thenData[1][channelIndex].snippet.thumbnails['high'].url,
                                type: 'channel',
                                viewCount: thenData[1][channelIndex].statistics.viewCount,
                                commentCount: thenData[1][channelIndex].statistics.commentCount,
                                subscriberCount: thenData[1][channelIndex].statistics.subscriberCount,
                                hiddenSubscriberCount: thenData[1][channelIndex].statistics.hiddenSubscriberCount,
                                videoCount: thenData[1][channelIndex].statistics.videoCount,
                                locale: {
                                    subscriberCount: thenData[1][channelIndex].statistics.subscriberCount + ' ' + ngettext('subscriber', 'subscribers', +thenData[1][channelIndex].statistics.subscriberCount)
                                }
                            });
                            ++channelIndex;
                        } else if ( playlists[i] && thenData[2][playlistIndex] ) {
                            result.push({
                                value: 1,
                                playlistId: thenData[2][playlistIndex].id,
                                channel: {
                                    title: thenData[2][playlistIndex].snippet.channelTitle,
                                    id: thenData[2][playlistIndex].snippet.channelId
                                },
                                title: thenData[2][playlistIndex].snippet.title,
                                icon: thenData[2][playlistIndex].snippet.thumbnails['high'].url,
                                type: 'playlist',
                                channelTitle: thenData[2][playlistIndex].snippet.channelTitle,
                                viewCount: ' ',
                                duration: ' ',
                                publishedAt: thenData[2][playlistIndex].snippet.publishedAt,
                                locale: {
                                    publishedAt: timeDiff(thenData[2][playlistIndex].snippet.publishedAt, currentDate),
                                    viewCount: ' ',
                                    channelTitle: thenData[2][playlistIndex].snippet.channelTitle ? l10nAuthor + ': ' + thenData[2][playlistIndex].snippet.channelTitle : ' '
                                }
                            });
                            ++playlistIndex;
                        } else if ( thenData[0][videoIndex] ) {
                            result.push({
                                value: 1,
                                id: thenData[0][videoIndex].id,
                                channelTitle: thenData[0][videoIndex].snippet.channelTitle,
                                duration: api.normalizeVideoDuration(thenData[0][videoIndex].contentDetails.duration),
                                realDuration: thenData[0][videoIndex].contentDetails.duration,
                                viewCount: thenData[0][videoIndex].statistics.viewCount,
                                publishedAt: thenData[0][videoIndex].snippet.publishedAt,
                                dimension: thenData[0][videoIndex].contentDetails.dimension,
                                definition: thenData[0][videoIndex].contentDetails.definition,
                                title: thenData[0][videoIndex].snippet.localized.title,
                                icon: thenData[0][videoIndex].snippet.thumbnails['high'].url,
                                channelId: thenData[0][videoIndex].snippet.channelId,
                                type: 'video',
                                locale: {
                                    publishedAt: timeDiff(thenData[0][videoIndex].snippet.publishedAt, currentDate),
                                    viewCount: ngettext('view', 'views', +thenData[0][videoIndex].statistics.viewCount) + ' ' + thenData[0][videoIndex].statistics.viewCount,
                                    channelTitle: l10nAuthor + ': ' + thenData[0][videoIndex].snippet.channelTitle
                                }
                            });
                            ++videoIndex;
                        }
                    }
                    resolve(result);
                }, function ( error ) {
                    console.log(error);
                })['catch'](function ( error ) {
                    console.log(error);
                });
            }
        })['catch'](function ( error ) {
            console.log(error);
        });
    });
};


SearchModel.prototype.getChannelsInfo = function ( id ) {
    if ( !id ) {
        return Promise.resolve([]);
    }

    return api.request('GET', 'channels?part=snippet,statistics&id=' + id).then(function ( data ) {
        return JSON.parse(data).items;
    });
};


/**
 *
 * @param {Object} [params={}] filter parameters
 * @param {Object} [params.channel] channel object with id and name
 * @param {string} [params.searchQuery] search query
 * @param {number} [params.relatedToVideoId] pass this param add video relation option
 *
 * @fires SearchModel#content:changed
 *
 * @return {boolean} applied filter
 */
SearchModel.prototype.filter = function ( params ) {
    var changed = false;

    if ( params.channel !== undefined ) {
        this.init({channel: params.channel});
    }
    if ( params.searchQuery !== undefined && this.searchQuery !== params.searchQuery ) {
        changed = true;
        this.searchQuery = params.searchQuery;
    }
    if ( params.relatedToVideoId !== undefined && this.relatedToVideoId !== params.relatedToVideoId ) {
        changed = true;
        this.relatedToVideoId = params.relatedToVideoId;
    }
    if ( params.order !== undefined && this.order !== params.order ) {
        changed = true;
        this.order = params.order;
    }
    if ( params.type !== undefined && this.type !== params.type ) {
        changed = true;
        this.type = params.type;
    }
    if ( changed ) {
        this.pages = {};
        this.emit('content:changed', params);

        return true;
    }

    return false;
};

// public export
module.exports = SearchModel;
