'use strict';

var Widget = require('spa-component-widget'),
    loader = new Widget({
        $node: document.getElementById('loaderWidget'),
        visible: false
    }),

    iterationTimeout = -1,
    currentIndex = 0,
    ready = false,

    imgs = [];

(function () {
    var total = 4;

    [
        'img/loader/1.png',
        'img/loader/2.png',
        'img/loader/3.png',
        'img/loader/4.png'
    ].forEach(function ( item ) {
        var img = new Image();

        img.src = item;
        img.onload = function () {
            --total;
            if ( total === 0 ) {
                ready = true;
            }
        };
        imgs.push(img);
    });
})();

function imageLoop () {
    if ( ready ) {
        loader.$node.style.backgroundImage = 'url(' + imgs[currentIndex].src + ')';
        ++currentIndex;
        if ( currentIndex === 4 ) {
            currentIndex = 0;
        }
    }
    iterationTimeout = setTimeout(imageLoop, 200);
}


loader.show = function ( data ) {
    // is it hidden
    if ( !this.visible ) {
        // correct style
        this.$node.classList.remove('hidden');
        // flag
        this.visible = true;

        // there are some listeners
        if ( this.events['show'] !== undefined ) {
            /**
             * Make the component visible.
             *
             * @event module:stb/component~Component#show
             */
            this.emit('show', data);
        }
        iterationTimeout = setTimeout(imageLoop, 200);

        return true;
    }

    // nothing was done
    return true;
};

loader.hide = function () {
    currentIndex = 1;
    clearTimeout(iterationTimeout);
    // is it visible
    if ( this.visible ) {
        // correct style
        this.$node.classList.add('hidden');
        // stop iteration
        // flag
        this.visible = false;

        // there are some listeners
        if ( this.events['hide'] !== undefined ) {
            /**
             * Make the component hidden.
             *
             * @event module:stb/component~Component#hide
             */
            this.emit('hide');
        }

        return true;
    }

    // nothing was done
    return true;
};

module.exports = loader;
