/**
 * Base video data provider base model.
 *
 * @author Igor Zaporozhets <deadbyelpy@gmail.com>
 */

'use strict';

var Promise = require('../../promise'),

    Emitter = require('cjs-emitter'),

    api = require('./../client.js'),

    l10n = false,
    timeDiff,
    l10nAuthor,

    cache = require('../../util/cache');


function cacheKey ( params ) {
    if ( params && params.playlist ) {
        return 'pid:' + params.playlist.id + ';p:' + params.page;
    }

    return 'PLAYLIST';
}


/**
 *
 * @constructor
 *
 * @param {Object} config init config
 */
function PlaylistModel ( config ) {
    this.pages = {};

    this.playlist = null;

    Emitter.call(this);

    config = config || {};

    if ( config.events !== undefined ) {
        this.addListeners(config.events);
    }

    this.init(config);
}

// inheritance
PlaylistModel.prototype = Object.create(Emitter.prototype);
PlaylistModel.prototype.constructor = PlaylistModel;

PlaylistModel.prototype.getPage = function ( params ) {
    var self = this,
        fromCache;

    params.playlist = params.playlist || this.playlist;
    params.page = +params.page || 0;
    params.count = params.count || 20;

    return new Promise(function ( resolve, reject ) {
        fromCache = cache.get(cacheKey(params));
        if ( fromCache ) {
            resolve(fromCache);
        } else {
            if ( !params.playlist.id ) {
                reject(params);

                return;
            }
            self.getPlayListItems(params).then(resolve, reject);
        }
    });
};


PlaylistModel.prototype.getPlayListItems = function ( params ) {
    var self = this,
        videos = [],
        ids = '',
        url = 'playlistItems?part=snippet&playlistId=' + params.playlist.id + '&maxResults=' + (params.count || 30);

    params.page = +params.page || 0;

    if ( !l10n ) {
        l10n = require('../../util/l10n');
        timeDiff = require('../../util/time.diff');
        l10nAuthor = gettext('Author');
    }

    if ( params.page ) {
        if ( !self.pages[params.page] ) {
            return Promise.reject();
        }
        url += '&pageToken=' + self.pages[params.page];
    }

    return api.request('GET', url)
        .then(function ( data ) {
            try {
                data = JSON.parse(data);
                if ( data.nextPageToken ) {
                    self.pages[params.page + 1] = data.nextPageToken;
                }
                if ( data.prevPageToken ) {
                    self.pages[params.page - 1] = data.prevPageToken;
                }
                data.items.forEach(function ( item ) {
                    ids += item.snippet.resourceId.videoId + ',';
                });
            } catch ( error ) {
                console.log(error);
            }

            return self.getMovies(ids.substr(0, ids.length - 1)).then(function ( moviesData ) {
                var currentDate = +new Date(),
                    size, index;

                size = moviesData.length;
                for ( index = 0; index < size; ++index ) {
                    videos.push({
                        value: 1,
                        id: moviesData[index].id,
                        channelTitle: moviesData[index].snippet.channelTitle,
                        duration: api.normalizeVideoDuration(moviesData[index].contentDetails.duration),
                        realDuration: moviesData[index].contentDetails.duration,
                        viewCount: moviesData[index].statistics.viewCount,
                        publishedAt: moviesData[index].snippet.publishedAt,
                        dimension: moviesData[index].contentDetails.dimension,
                        definition: moviesData[index].contentDetails.definition,
                        title: moviesData[index].snippet.localized.title,
                        icon: moviesData[index].snippet.thumbnails['high'].url,
                        channelId: moviesData[index].snippet.channelId,
                        type: 'video',
                        locale: {
                            publishedAt: timeDiff(moviesData[index].snippet.publishedAt, currentDate),
                            viewCount: moviesData[index].statistics.viewCount + ' ' +
                                ngettext('view', 'views', +moviesData[index].statistics.viewCount),
                            channelTitle: l10nAuthor + ': ' + moviesData[index].snippet.channelTitle
                        }
                    });
                }
                cache.set(cacheKey(params), videos, 60 * 1000 * 5);

                return videos;
            });
        });
};

PlaylistModel.prototype.getMovies = function ( id ) {
    var result = [],
        video, ids, index, size;

    ids = id.split(',');
    for ( index = 0, size = ids.length; index < size; ++index ) {
        video = cache.get('vid:' + ids[index]);
        if ( video ) {
            result.push(video);
        }
    }

    return api.request('GET', 'videos?part=statistics,contentDetails,snippet&id=' + id).then(function ( data ) {
        data = JSON.parse(data).items;

        for ( index = 0, size = data.length; index < size; ++index ) {
            cache.set('vid:' + data[index].id, data[index], 1000 * 60);
        }

        return data;
    });
};


PlaylistModel.prototype.init = function ( params ) {
    if ( params.playlist !== undefined ) {
        if ( !this.playlist ) {
            this.playlist = params.playlist;
        } else if ( this.playlist && this.playlist.id !== params.playlist.id ) {
            this.playlist = params.playlist;
        }
        this.pages = {};
        this.emit('content:changed', params);

        return true;
    }

    return false;
};

// public export
module.exports = PlaylistModel;
